$(".footer-group__show").click(function () {
    $(this).parents(".footer-group__item").toggleClass("_active")
    $(this).siblings(".footer-group__hidden").slideToggle()
})

$(".footer-group__top").click(function () {
    if(window.innerWidth<=576){
        $(this).parents(".footer-group").toggleClass("_active")
        $(this).siblings(".footer-group__bottom").slideToggle()
    }
})
