let userMenuToggle = document.querySelectorAll("._toggle-user-menu")
let body = document.querySelector("body")
let header = document.querySelector(".header")
let menuClose = document.querySelector(".menu__bg")


if (userMenuToggle) {
    userMenuToggle.forEach(btn => {
        btn.onclick = function() {
            header.classList.toggle("_open-user-menu")
            body.classList.toggle("fixed-body")
        }
    })

}

if(menuClose){
    menuClose.onclick = function () {
        closeMenu()
    }
}

function closeMenu(){
    header.classList.remove("_open")
    body.classList.remove("fixed-body")
}




let headerUser = document.querySelector(".header-user")
let headerUserToggle = document.querySelector(".header-user__toggle")
if(headerUserToggle){
    headerUserToggle.onclick = function () {
        headerUser.classList.toggle("_active")
    }
    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(headerUser);

        if ( ! withinBoundaries ) {
            headerUser.classList.remove("_active")
        }
    })
}


$("._show-additional-menu").click(function () {
    $(".header").toggleClass("_open-additional-menu")
    if(window.innerWidth<=992){
        $(this).siblings(".nav__hidden").slideToggle()
    }
})

$(document).mouseup( function(e){
    let div = $( ".header" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(".header").hasClass("_open-additional-menu")){
            $(".header").removeClass("_open-additional-menu")
        }
    }
});

$("._toggle-burger-menu").click(function () {
    $(".header").toggleClass("_open-burger-menu")
    $("body").toggleClass("fixed-body")
})
